<template>
  <div class="">
    <div class="header">
      <!-- mobile version -->
      <div class="d-block d-md-none ">
        <div class="header__title bg-darkpurple">HaveFun</div>
        <div class="header__search position-relative p-3 ">
          <i class="fas fa-search   position-absolute text-black" style="left: 40px; top: 50%;transform: translateY(-50%)"></i>
          <input type="text" placeholder="Explore your own activities" class="form-control form-control  pl-5 ">

        </div>
      </div>

      <!-- up version -->
      <div class="bg-darkpurple d-none d-md-flex px-5 align-items-center ">
        <div class="header__title bg-darkpurple grid300">HaveFun</div>
        <div class="header__search position-relative ">
          <i class="fas fa-search   position-absolute text-white" style="left: 50px; top: 50%;transform: translateY(-50%)"></i>
          <input type="text" placeholder="Explore your own activities" class="form-control form-control-lg border-top-0 border-left-0 border-right-0 pl-5  bg-transparent"
            style="width:388px;color: rgb(255,255,255,.5)">
        </div>
      </div>
    </div>
    <div class="filter d-block d-md-none">
      <!-- mb -->
      <!-- filter-location -->
      <div class="filter__section ">
        <div class="section--open" v-show="filter=='location'">
          <div class="d-flex justify-content-between">
            <span class="title">Location</span>
            <span @click="filter=''">-</span>
          </div>
          <div class="d-flex justify-content-between">
            <span class="text">Tainwa</span>
            <span>+</span>
          </div>
        </div>
        <div class="section--close" v-show="filter!='location'" @click="filter='location'">
          <div class="d-flex justify-content-between">
            <span class="title">Location</span>
            <span>+</span>
          </div>
        </div>

      </div>
      <!-- filter-Date -->
      <div class="filter__section">
        <div class="section--open" v-show="filter=='date'">
          <div class="d-flex justify-content-between">
            <span class="title">Date</span>
            <span @click="filter=''">-</span>
          </div>
          <div class="d-flex justify-content-between">
            <span class="text">Tainwa</span>
            <span>+</span>
          </div>
        </div>
        <div class="section--close" v-show="filter!='date'" @click="filter='date'">
          <div class="d-flex justify-content-between">
            <span class="title">Date</span>
            <span>+</span>
          </div>
        </div>

      </div>
      <!-- filter-categories -->
      <div class="filter__section">
        <div class="section--open" v-show="filter=='categories'">
          <div class="d-flex justify-content-between">
            <span class="title">Categories</span>
            <span @click="filter=''">-</span>
          </div>
          <div class="d-flex justify-content-between">
            <span class="text">Tainwa</span>
            <span>+</span>
          </div>
        </div>
        <div class="section--close" v-show="filter!='categories'" @click="filter='categories'">
          <div class="d-flex justify-content-between">
            <span class="title">Categories</span>
            <span>+</span>
          </div>
        </div>

      </div>
    </div>
  <div class="container-fluid px-0">
    <div class="row mx-0">
      <div class="filter--pc col-12 col-md-4 col-xl-2 d-none d-md-block ">
      <!-- pc -->
      <!-- <div class="filter--pc__section">
        <h1 class="title">Location</h1>
        <span>+++</span>
      </div> -->
      <div class="filter--pc__section">
        <h1 class="title">Zone</h1>
        <div v-for="(item, index) in ksZone" :key="index">
        <input type="checkbox" :id="index" :value="item" v-model="zoneSelected">
        <label :for="index">{{item}}</label>
        <br>
        </div>
        
        <!-- <span>Picked: {{ zoneSelected }}</span> -->
      </div>
      <!-- <div class="filter--pc__section">
        <h1 class="title">Catgory</h1>
        <select v-model="categorySelected">
        <option disabled value="">Please select one</option>
        <option>A</option>
        <option>B</option>
        <option>C</option>
      </select>
        <span>Selected: {{ categorySelected }}</span>
      </div> -->
     
    </div>

    <div class="cards col-12 col-md-8 col-xl-10 border-transparent">
        <div class="">
            <div class="card mb-3  flex-xl-row" v-for="(item, index) in finalData" :key="index">
                <img class="card__img__top col-12 col-xl-4" :src="item['Picture1']">
                <div class="card-body col-12 col-xl-8 border-0">
                  <h5 class="card__title">{{item['Name']}}</h5>
                  <p class="card__text">{{item['Description']}}</p>
                  <span class="card__category">{{item['Class1']}}</span>
                  <p class="card__city"><i class="fas fa-map-marker-alt pr-2"></i>{{item['Zone']}}</p>
                  <p class="card__period"><i class="far fa-calendar-alt pr-2"></i>{{item['Opentime']}}</p>
                </div>
              </div>
        </div>
    </div>
    </div>
  </div>
   
 <paginate class="justify-content-end" :click-handler="clickCallback" :page-count="pageCount" :initial-page="init"   :prev-text="'Prev'" :next-text="'Next'"
        :container-class="'pagination'" :page-class="'page-item'" :page-link-class="'page-link'" :prev-class="'page-item'"
        :prev-link-class="'page-link'" :next-class="'page-item'" :next-link-class="'page-link'" ref="paginate">
      </paginate>
  </div>
</template>

<script>
export default {
  name: "HelloWorld",
  data() {
    return {
      checked: true,
      filter: "",
      fakedata: {},
      pageNum:1,
      pageCount:10,
      perPage:5,
      init:0,
      pageData: [],
      catgories: [],
      categorySelected:'',
      zoneSelected:[],
      ksZone:['楠梓區','左營區','鼓山區','三民區','苓雅區','新興區','前金區','鹽埕區','前鎮區','旗津區','小港區'],
    };
  },
  computed: {
    inject() {
      this.pageData.forEach(element => {
        let number = element["Class1"];
        let target = this.catgories.find(function(item, index, array) {
          return item.value == number;
        });
        element["Class1"] = target.text;
      });
      // console.log(this.pageData)
    },
    filtedData(){
        let zone = this.zoneSelected;
        let temp=[];
  if(zone.length!=0 ){
    this.pageData.forEach(element => {
        for(let i=0;i<zone.length;i++){
          if(element['Zone']==zone[i]){
            temp.push(element);
          }
        }
      
         });
  }
        else{
          temp=this.pageData;
        }
       return temp;
    },
    finalData(){
      let pageCount=Math.floor(this.filtedData.length/this.perPage);
      if(this.filtedData.length%this.perPage!=0){
        pageCount+=1;
      }
      console.log('final');
      console.log(pageCount);
      this.pageCount=pageCount;
      let temp=[];
      let index=this.pageNum-1;
      for(let i=0;i<this.perPage;i++){
        if(this.filtedData[index*this.perPage+i]!=null){
        temp.push(this.filtedData[index*this.perPage+i]);
        }
        
      }
      console.log(temp);
      return temp;
    }
  },
  watch:{
    zoneSelected(){
      this.$refs.paginate.selected = 0;
      this.pageNum=1;
    }
  },
  methods: {
    clickCallback(pageNum){
      this.pageNum=pageNum;
      console.log(pageNum);
    }
    ,
    getData() {
      let vm = this;
       

        vm.axios
        .get(
          "https://data.kcg.gov.tw/api/action/datastore_search?resource_id=92290ee5-6e61-456f-80c0-249eae2fcc97",
          {
            params: {
              
            }
          }
        )
        .then(function(response) {
          let root = response.data.result.records;
          // console.log(root);
          root.forEach(item => {
            vm.pageData.push(item);
          });
         
          // console.log(vm.pageData);
          vm.getCatgories();
        });
           
      
    },
    getCatgories() {
      let vm = this;
       
      
        vm.axios
        .get("https://codepen.io/peian/pen/qKmBGm.js")
        .then(function(response) {
          // console.log(response);
          let root = response.data;
          // console.log(root);
          root.forEach(item => {
            vm.catgories.push(item);
          });
          console.log("2");
          
          vm.injectCatgory();
        });
            
      
    },
    injectCatgory() {
      console.log('3');
      this.pageData.forEach(element => {
        let number = element["Class1"];
        let target = this.catgories.find(function(item, index, array) {
          return item.value == number;
        });
        element["Class1"] = target.text;
      });
    },
    dosome(){
      alert('hi');
    }
  },
  mounted() {
    this.getData();
    // Promise.all([ this.getData(), this.getCatgories() ]).then(this.injectCatgory());
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang='scss'>
</style>



